import axios from 'axios'
import { Notification, MessageBox, Message, Loading } from 'element-ui'
import store from '@/store'
import { getToken, removeToken } from '@/utils/auth'

import { tansParams, hint } from '@/utils/index'

const baseURL = window.config.baseUrl

axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8'
// axios
const service = axios.create({
  // axiosbaseURLURL
  baseURL,
  // 
  timeout: 10000,
})

// request
service.interceptors.request.use(
  (config) => {
    config.baseURL = window.config[config.request_base] || baseURL

    //  token
    const { isToken } = config.headers || {}

    if (getToken() && !isToken) {
      config.headers.Authorization = `Bearer ${getToken()}` // token 
    }
    // console.log(config, 'request')
    // getparams
    if (config.params) {
      let url = `${config.url}?${tansParams(config.params)}`
      url = url.slice(0, -1)
      config.params = {}
      config.url = url
    }

    return config
  },
  (error) => {
    Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  (res) => {
    //   
    const { code, msg } = res.data

    // console.log(res, res.headers, res.headers['Content-Disposition'], 'request')

    // 
    if (
      res.request.responseType === 'blob' ||
      res.request.responseType === 'arraybuffer'
    ) {
      return res.data
    }

    if (res.status === 200 || res.data.code === 0) {
      return Promise.resolve(res.data)
    }
    if (res.data.code === -2) {
      hint(res.data.data, store.state.setting.doStatusShow)
    } else {
      // Message({
      //   message: msg,
      //   type: 'error',
      // })
      return Promise.reject(res)
    }
  },
  (error) => {
    const { config, response } = error
    let { message } = error
    // console.log(config, response, message, 'error')
    if (response) {
      if (
        response.status === 401 ||
        response.status === 472 ||
        response.status === 471
      ) {
        if (config.url !== '/Token/Logout' || config.url !== '/Token/Access') {
          // let msg = response.status === 401?'':''
          MessageBox.confirm(
            '',
            '',
            {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning',
            }
          )
            .then(() => {
              removeToken()
              store.dispatch('LogOut').then(() => {})
            })
            .catch(() => {})
        }
      }
    }

    if (message === 'Network Error') {
      message = ''
    } else if (message.includes('timeout')) {
      message = ''
    }
    // Message({
    //   message,
    //   type: 'error',
    //   duration: 5 * 1000,
    // })
    return Promise.reject(error)
  }
)

export default service
