/**
 * 
 */
const monitor = {
  state: {
    unusual: '', // 
    hourly: '', // 
    freq: '', // 
  },

  mutations: {
    SET_UNUSUAL: (state, unusual) => {
      state.unusual = unusual
    },
    SET_HOURLY: (state, hourly) => {
      state.hourly = hourly
    },
    SET_FREQ: (state, freq) => {
      state.freq = freq
    },
  },
}

export default monitor
