export class Control {
  //#region 
  constructor(map,layer) {
    /**
     * Cesium Viewer
     * */
    this._map = map
    /**
     * 
     * */
    this._layer = layer
  }
  //#endregion 

  // map 
  bindMapMenu(menuItems) {
    console.log(this._map);
    const contextmenuItems = menuItems || this._map.getDefaultContextMenu() // 
    console.log('contextmenuItems',contextmenuItems)
    this._map.bindContextMenu(contextmenuItems) 
  }
  // Map
  unBindMapMenu() {
    this._map.unbindContextMenu()
  }

  // layer
  bindLayerMenu(graphicLayer,menuItems) {
    
    graphicLayer = graphicLayer || this._layer
    console.log('graphicLayer',graphicLayer);
    menuItems = menuItems || [
      {
        text: "",
        icon: "fa fa-edit",
        show: function (e) {
          const graphic = e.graphic
          if (!graphic || !graphic.startEditing) {
            return false
          }
          return !graphic.isEditing
        },
        callback: function (e) {
          const graphic = e.graphic
          if (!graphic) {
            return false
          }
          if (graphic) {
            graphicLayer.startEditing(graphic)
          }
        }
      },
      {
        text: "",
        icon: "fa fa-edit",
        show: function (e) {
          const graphic = e.graphic
         
          if (!graphic) {
            return false
          }
          return graphic.isEditing
        },
        callback: function (e) {
          const graphic = e.graphic
          if (!graphic) {
            return false
          }
          if (graphic) {
            // if(graphic.options.type === 'UAV'){
              // let options_UAV = graphic.options
              // console.log(graphic,graphic.point,options_UAV);
              // console.log(options_UAV.positions.getValue());
              // if(options.lng && options.lat && options.alt){
              //   options_UAV.position = [options.lng, options.lat, options.alt];

              //   options_UAV.style.label.text = new window.cesium.CallbackProperty(()=>{
              //     return `${options.lng.toFixed(6)}\n${options.lat.toFixed(6)}\n${options.alt.toFixed(0)}`;
              //   }, false).getValue();
              // }
            // }
            graphicLayer.stopEditing(graphic)
          }
        }
      },
      {
        text: "",
        icon: "fa fa-trash-o",
        callback: function (e) {
          const graphic = e.graphic
          if (graphic) {
            graphicLayer.removeGraphic(graphic)
          }
        }
      },
      {
        text: "",
        icon: "fa fa-medium",
        show: function (e) {
          const graphic = e.graphic
          return (
            graphic.type === "scrollWall" ||
            graphic.type === "polyline" ||
            graphic.type === "curve" ||
            graphic.type === "polylineVolume" ||
            graphic.type === "corridor" ||
            graphic.type === "wall" 
          )
        },
        callback: function (e) {
          const graphic = e.graphic
          const strDis = mars3d.MeasureUtil.formatDistance(graphic.distance)
          alert(":" + strDis)
        }
      },
      {
        text: "",
        icon: "fa fa-medium",
        show: function (e) {
          const graphic = e.graphic
          return graphic.type === "circle" || graphic.type === "rectangle" || graphic.type === "polygon"
        },
        callback: function (e) {
          const graphic = e.graphic
          const strDis = mars3d.MeasureUtil.formatDistance(graphic.distance)
          alert(":" + strDis)
        }
      },
      {
        text: "",
        icon: "fa fa-reorder",
        show: function (e) {
          const graphic = e.graphic
          return graphic.type === "circle" || graphic.type === "rectangle" || graphic.type === "polygon"
        },
        callback: function (e) {
          const graphic = e.graphic
          const strArea = mars3d.MeasureUtil.formatArea(graphic.area)
          alert(":" + strArea)
        }
      }
    ]
    graphicLayer.bindContextMenu(menuItems)
  }
  // layer
  unBindLayerMenu(graphicLayer) {
    graphicLayer = graphicLayer || this._layer
    graphicLayer.unbindContextMenu()
  }
  //  graphic
  bindGraphicMenu(graphic) {
    graphic.bindContextMenu([
      {
        text: 'graphic',
        icon: 'fa fa-trash-o',
        callback: function (e) {
          const graphic = e.graphic
          if (graphic) {
            graphic.remove()
          }
        },
      },
    ])
  }
  // Map
  unBindGraphicMenu(graphic) {
    graphic.unbindContextMenu()
  }
}