import user from './user'

const websocket = {
  state: {
    serverWs: null,
    serverdata: null,
    uavListArr: [], // 
    uavListAll: [], // 
    notificationList: [], // 
    heatBeatList: [], // 
    wsMsgStatus: {
      // 
      heartbeat: false, // 
      workmode: false, // 
      findTarget: false, // 
      locus: false, // 
      push_orient: false, // 
      jamminResult: false, // 
      push_img: false, // 
      carLocation: false, // 
    },
  },

  mutations: {
    SET_SERVERWS: (state, data) => {
      state.serverWs = data
    },
    SET_SERVERDATA: (state, serverdata) => {
      const loginState = user.state.isLogin
      state.serverdata = loginState
        ? serverdata
        : {
            data: {},
          }
    },
    SET_UAVLISTARR: (state, data) => { 
      state.uavListArr = data
    },
    SET_UVALISTALL: (state, data) => {
      state.uavListAll = data
    },
    SET_NOTIFICATION: (state, data) => {
      state.notificationList = data
    },
    SET_HEATBEATLIST: (state, data) => {
      state.heatBeatList = data
    },
  },
}

export default websocket
