import Cookies from 'js-cookie'
import { Message } from 'element-ui'
import { login, logout, getAccessToken } from '@/api/login'
import { setToken, removeToken } from '@/utils/auth'
import { cryptoEncryption } from '@/utils/cryptojs'

const user = {
  state: {
    userId: '',
    isLogin: false,
    isFull: false, // 
    disabledState: false, // 
  },
  mutations: {
    SET_USERID: (state, userId) => {
      state.userId = userId
    },
    SETISLOGIN: (state, isLogin) => {
      state.isLogin = isLogin
    },
    SET_ISFULL: (state, isFull) => {
      state.isFull = isFull
    },
    SET_DISABLEDSTATE: (state, disabledState) => {
      state.disabledState = disabledState
    },
  },
  actions: {
    // 
    Login({ dispatch, commit }, userInfo) {
      const username = userInfo.username.trim()
      const password = cryptoEncryption(userInfo.password.trim())
      // const password = userInfo.password.trim()
      const { type } = userInfo
      const { vertify_code } = userInfo
      return new Promise((resolve, reject) => {
        login(username, password, type, vertify_code)
          .then((res) => {
            // userid
            commit('SET_USERID', res.data.userid)
            // token -> Access /  
            Cookies.set('logintoken', res.data.token)
            if (
              res.data.loingLock ||
              res.data.loginLongTerm ||
              res.data.loginFaild
            ) {
              reject(res)
            } else {
              if (res.data.loginTip) {
                Message({
                  message: res.data.loginTip,
                  type: 'warning',
                })
              }
              dispatch('GetAccessToken')
                .then(() => {
                  resolve()
                })
                .catch((error) => {
                  reject(error)
                })
            }
          })
          .catch((error) => {
            reject(error)
          })
      })
    },
    // token
    GetAccessToken({ state, commit }) {
      // token 5
      function refreshToken(time = 5000) {
        // 
        setTimeout(() => {
          getAccessToken().then((res) => {
            if (res.code === 0) {
              setToken(res.data.token)

              //  timestamp  1
              const currentTime = new Date().getTime()
              let { timestamp } = res.data
              if (timestamp < currentTime) {
                timestamp *= 1000
              }

              const diff = timestamp - currentTime - 60000 // 60
              refreshToken(diff)

              // if (router.currentRoute.path !== '/login') {
              //   refreshToken(diff)
              // }
            }
          })
        }, time)
      }
      const promise = new Promise((resolve, reject) => {
        getAccessToken().then((res) => {
          if (res.code === 0) {
            state.isLogin = true // 

            setToken(res.data.token)
            //  timestamp  1
            const currentTime = new Date().getTime()
            let { timestamp } = res.data
            if (timestamp < currentTime) {
              timestamp *= 1000
            }
            const diff = timestamp - currentTime - 60000 // 60
            refreshToken(diff)
          }

          resolve()
        })
      })
      return promise
    },

    // 
    LogOut({ commit, state }) {
      removeToken()
      Cookies.remove('logintoken')
      state.isLogin = true // 
      return new Promise((resolve, reject) => {
        logout()
          .then((res) => {
            resolve()
          })
          .catch((error) => {
            reject(error)
          })
      })
    },
  },
}

export default user
